    transform: scale(1);
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1.2);
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    -ms-transform: scale(1.2);
    -o-transform: scale(1.2);
  }
}

.fluent-content-dialog-background {
  background-color: rgba(0, 0, 0, 0.175);
  position: fixed;
  display: none;
  justify-content: center;
  align-items: center;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.fluent-content-dialog {
  background-color: var(--dark-color);
  border-radius: 10px;
  border: var(--lighter-border-color) solid 1px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  width: fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  padding: 1.35em 1.5em 2em 1.5em;
  min-width: 15em;
  min-height: 8em;
  overflow: hidden;
  box-shadow: 0px 5px 13px 5px rgba(0, 0, 0, 0.1);
}

.fluent-content-dialog * {
  flex-grow: 1;
}

fluent-content-dialog-options {
  background-color: var(--darker-color-noa);
  border-top: var(--dark-border-color) solid 1px;
  width: auto;
  height: fit-content;
  margin: 1.5em -1.5em -2em -1.5em;
  display: flex;
  justify-content: stretch;
  flex-direction: row;
  align-items: center;
  padding: 0.5em;
}

fluent-content-dialog-options * {
  flex: 1 1 0px;
  margin: auto 0.5em;
}

/* Icon */

.fluent-icon {
  filter: invert(var(--icon-color-inverted));
  -webkit-filter: invert(var(--icon-color-inverted));
  pointer-events: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
  transition: 0.2s ease-in-out;
  -webkit-transition: 0.2s ease-in-out;
  -moz-transition: 0.2s ease-in-out;
  -ms-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
}

/* Responsivity */

.fluent-responsive-title-container {
  display: none;
}

fluent-title-responsive {
  display: none;
}

.fluent-responsive-menu-button {
  display: none;
}

@media only screen and (max-width: 1000px) {
  .fluent-responsive-title-container {
    z-index: 100;
    background-color: var(--darker-color);
    width: 100%;
    top: 0;
    position: fixed;
    border-bottom: 1px solid var(--dark-border-color);
    flex-grow: 1 0;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-flow: column;
    backdrop-filter: blur(40px);
    -webkit-backdrop-filter: blur(40px);
    transition: all 0.2s ease-in-out;
    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -ms-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
  }

  .fluent-responsive-title-container.open {
    background-color: var(--darker-color-noa);
    bottom: 0;
  }

  .fluent-responsive-title-container.open .fluent-menu {
    border-radius: 0;
    position: relative;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }

  .fluent-responsive-title-container.open ul {
    margin-bottom: 0;
    width: 99.5%;
    position: absolute;
    overflow-y: auto;
    overflow-x: hidden;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    backdrop-filter: unset;
    -webkit-backdrop-filter: unset;
    border-radius: 0;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    -ms-border-radius: 0;
    -o-border-radius: 0;
  }

  .fluent-responsive-title-container.open li {
    position: relative;
  }

  .fluent-responsive-title-container.open .fluent-menu-item-expander {
    position: relative;
  }

  .fluent-responsive-title-container.open #fluent-main-menu {
    display: block !important;
  }

  .fluent-responsive-title-bar {
    width: 100%;
    display: flex;
    align-items: row;
  }

  .fluent-responsive-title {
    width: 100%;
    display: flex;
    align-items: row;
    justify-content: space-between;
  }

  #fluent-main-menu {
    width: 100% !important;
    height: 100%;
    display: none;
    padding: 0;
    margin-left: 0;
  }

  #fluent-title {
    display: none;
  }

  fluent-title {
    display: none !important;
  }

  body {
    margin-top: 5em;
  }

  fluent-title-responsive {
    display: block;
    margin: 0.5em;
  }

  fluent-title-responsive h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: auto 0.5em;
  }

  .fluent-responsive-menu-button {
    display: none;
  }

  /* Main Content */

  .fluent-main-content {
    border-radius: 8px 8px 0 0;
    -webkit-border-radius: 8px 8px 0 0;
    -moz-border-radius: 8px 8px 0 0;
    -ms-border-radius: 8px 8px 0 0;
    -o-border-radius: 8px 8px 0 0;
  }

  body {
    zoom: 3;
    -ms-zoom: 3;
    -webkit-zoom: 3;
  }
}

/* Title */
fluent-title {
  display: block;
  margin-left: 5em;
}
